import axios from "axios";
import { useEffect, useMemo, useState } from "react";

export interface Infrastructure {
  country: string;
  country2LetterCode: string;
  country3LetterCode: string;
  countryNumeric: number;
  transmissionGridCircuitLength_km: number;
  distributionGridCircuitLength_km: number;
  transmissionTransformerCapacity_gva: number;
  distributionTransformerCapacity_gva: number;
  generatorStepupTransformers_gva: number;
}

function isNumeric(value: string) {
  return /^-?\d+$/.test(value);
}
function csvJSON(csv: string, delimiter = "\t") {
  const lines = csv.replace(/\r/g, "").split("\n");
  const result = [];
  const headers = lines[0].split(delimiter);
  for (let i = 1; i < lines.length; i++) {
    if (!lines[i]) continue;
    const obj: any = {};
    const currentline: any = lines[i].split(delimiter);
    for (let j = 0; j < headers.length; j++)
      obj[headers[j]] = isNumeric(currentline[j])
        ? currentline[j] * 1
        : currentline[j];

    result.push(obj);
  }
  return result;
}

const infra = {
  country: "",
  country2LetterCode: "",
  country3LetterCode: "",
  countryNumeric: 0,
  transmissionGridCircuitLength_km: 0,
  distributionGridCircuitLength_km: 0,
  transmissionTransformerCapacity_gva: 0,
  distributionTransformerCapacity_gva: 0,
  generatorStepupTransformers_gva: 0,
};

const getInfrastructure = async (countryCode: string) => {
  return await axios
    .get("country_transmission.csv")
    .then((res) => csvJSON(res.data, ","))
    .then((data) => {
      return data.filter((el) => el["country2LetterCode"] === countryCode)[0];
    });
};

export function useInfrastructure(countryCode: string): [Infrastructure] {
  const [forcedInfrastructure, setInfrastructure] = useState(infra);
  useEffect(() => {
    async function fetchData() {
      const code = await getInfrastructure(countryCode);
      setInfrastructure(code);
    }
    fetchData();
  }, [countryCode]);

  const infrastructure = useMemo(() => {
    return forcedInfrastructure;
  }, [forcedInfrastructure]);

  return [infrastructure];
}
